import { Center, HStack, Text } from '@chakra-ui/react';
import React from 'react';

const data = [
  {
    title: '20+',
    text: 'Live    Agents',
  },
  {
    title: '5+',
    text: 'Years In Travel Market',
  },
  {
    title: '10K+',
    text: 'Clients Assisted',
  },
];

function StatisticCard() {
  return (
    <HStack
      w={'full'}
      gap={{ base: 3, md: 6 }}
      flexDir={{ base: 'column', lg: 'row' }}
    >
      {data?.map((el) => (
        <Center
          w={'full'}
          key={el.text}
          px={{ base: 4, lg: '40px' }}
          py={{ base: '17px', lg: 6 }}
          rounded={{ base: 16, lg: 24 }}
          h={{ base: 'auto', lg: '148px' }}
          gap={{ base: '11px', lg: '22px' }}
          boxShadow='0px 8px 30px #0000000D'
          flexDir={{ base: 'row', lg: 'column' }}
          justifyContent={{ base: 'start', lg: 'center' }}
        >
          <Text
            fontWeight={700}
            fontSize={{ base: 20, lg: 30 }}
            color={'primary'}
          >
            {el.title}
          </Text>
          <Text
            textAlign={'center'}
            maxW={{ base: 'full', lg: '114px' }}
            fontWeight={600}
          >
            {el.text}
          </Text>
        </Center>
      ))}
    </HStack>
  );
}

export default StatisticCard;
