import { Button, Container, Heading, HStack } from '@chakra-ui/react';
import React from 'react';
import CountryCarousel from './country-carousel';

function ReturnDeal() {
  return (
    <>
      <Container maxW={'1200px'}>
        <Heading
          textAlign={'center'}
          pt={{ base: 10, lg: 20 }}
          as={'h2'}
          fontSize={{ base: 18, lg: 44 }}
          fontWeight={700}
          mb={{ base: 4, lg: 8 }}
        >
          Return Deals of the Week
        </Heading>
      </Container>
      <Container px={{ base: 0, md: 8 }} maxW={'1200px'}>
        <HStack
          gap={'10px'}
          overflowX={{ base: 'auto', md: 'scroll' }}
          flexWrap={{ base: 'nowrap', md: 'wrap' }}
          w={'full'}
          scrollbar={'hidden'}
        >
          {Array.from({ length: 5 }).map((_, index) => (
            <Button
              key={index}
              size={'lg'}
              _hover={{ bgColor: 'primary.100', color: 'primary' }}
              rounded={14}
              variant={'subtle'}
            >
              Uzbekistan
            </Button>
          ))}
        </HStack>
        <CountryCarousel />
      </Container>
    </>
  );
}

export default ReturnDeal;
