import { Box, Button, Image, Stack, Text } from '@chakra-ui/react';
import React from 'react';

function BestCard() {
  return (
    <>
      <Box
        w={'full'}
        backgroundImage={'url(/main-hero.webp)'}
        bgSize={'cover'}
        bgPos={'center'}
        position={'relative'}
        bgRepeat={'no-repeat'}
        height={'360px'}
        rounded={24}
        display={{ base: 'none', md: 'block' }}
      >
        <Stack
          color={'light'}
          w={'full'}
          position={'absolute'}
          bottom={6}
          px={6}
        >
          <Text fontSize={28} fontWeight={600}>
            From $350
          </Text>
          <Text fontSize={20} fontWeight={600}>
            Uzbekistan
          </Text>
          <Button
            bg={'light'}
            rounded={12}
            w={'full'}
            color={'dark'}
            size={'xl'}
            variant={'solid'}
          >
            Get a quote
          </Button>
        </Stack>
      </Box>
      <Box display={{ base: 'block', md: 'none' }} fontWeight={600}>
        <Image
          w={'full'}
          minH={'174px'}
          rounded={16}
          src={'/main-hero.webp'}
          alt='pic'
        />
        <Text mt={2} fontSize={18}>
          From $400
        </Text>
        <Text color={'#535353'} fontSize={15}>
          Moscow
        </Text>
        <Button mt={2} w={'full'} rounded={12} variant={'subtle'}>
          Get a quote
        </Button>
      </Box>
    </>
  );
}

export default BestCard;
