import { Grid, Stack } from '@chakra-ui/react';
import ServiceCard from './card';

function ServiceCards() {
  const services = [
    {
      title: 'Daily Flight Options',
      imageSrc: '/service_img.png',
    },
    {
      title: 'Car Rentals',
      imageSrc: '/service_img.png',
    },
    {
      title: 'Cruises',
      imageSrc: '/service_img.png',
    },
    {
      title: 'Daily Tour Packages',
      imageSrc: '/service_img.png',
    },
  ];

  return (
    <Stack gap={{ base: 4, lg: 6 }}>
      {[0, 1].map((row) => (
        <Grid
          key={row}
          gap={{ base: 4, lg: 6 }}
          templateColumns={{
            base: '1fr',
            lg: row === 0 ? '6fr 4fr' : '4fr 6fr',
          }}
        >
          {services.slice(row * 2, row * 2 + 2).map((service, idx) => (
            <ServiceCard
              key={row * 2 + idx}
              title={service.title}
              imageSrc={service.imageSrc}
            />
          ))}
        </Grid>
      ))}
    </Stack>
  );
}

export default ServiceCards;
