import {
  Box,
  Container,
  HStack,
  Image,
  Separator,
  Stack,
  Text,
} from '@chakra-ui/react';
import React from 'react';
import StatisticCard from './components/statistic-card';
import InfoCard from './components/info-card';

function TravelProcess() {
  return (
    <Box bg={'gray.100'}>
      <HStack
        mt={20}
        position={'relative'}
        minH={{ base: '372px', lg: '478px' }}
        align={{ base: 'center', md: 'stretch' }}
      >
        <Container maxW={'1200px'} position={'relative'} zIndex={2}>
          <Box
            py={2}
            px={'20px'}
            display={{ base: 'none', md: 'flex' }}
            w={'165px'}
            bg={'white'}
            rounded={'full'}
            textAlign={'center'}
            transform={'translateY(-50%)'}
            boxShadow='0px 8px 30px #00000017'
          >
            <Image src={'/logo.png'} alt='logo' w={'125px'} />
          </Box>
          <HStack align={'flex-start'}>
            <Stack pt={{ base: 10, lg: 5 }} maxW={{ base: '100%', lg: '60%' }}>
              <Text fontSize={{ base: 18, lg: 44 }} fontWeight={700}>
                Call Us, Pack Up, Take Off
              </Text>
              <Text
                maxW={'484px'}
                fontWeight={500}
                mb={{ base: '20px', lg: 6 }}
                fontSize={{ base: 14, lg: 18 }}
                lineHeight={{ base: '20px', lg: '28px' }}
              >
                At TripOAsia, weâ€™re more than just a travel agencyâ€”weâ€™re your
                trusted travel partner. Hereâ€™s why travelers love booking with
                us
              </Text>

              <StatisticCard />
            </Stack>
          </HStack>
        </Container>
        <Image
          top={0}
          right={0}
          bottom={0}
          zIndex={1}
          h={'full'}
          alt='family'
          maxW={'717px'}
          src={'/family.png'}
          objectFit={'cover'}
          position={'absolute'}
          display={{ base: 'none', lg: 'block' }}
        />
      </HStack>

      <Box
        pt={4}
        bg={'gray.100'}
        textAlign={'center'}
        display={{ base: 'block', lg: 'none' }}
      >
        <Image
          h={'auto'}
          alt='family'
          maxW={'100%'}
          objectFit={'contain'}
          src={'/family_mobile.png'}
        />
      </Box>

      <Container display={{ base: 'none', lg: 'block' }} maxW={'1200px'}>
        <Separator size={'md'} colorPalette='#E4E4E4' />
      </Container>
      <Container py={{ base: '40px', lg: 20 }} maxW={'1200px'}>
        <InfoCard />
      </Container>
    </Box>
  );
}

export default TravelProcess;
