import { Container, HStack, Image, Text } from '@chakra-ui/react';
import React from 'react';

function Partners() {
  return (
    <Container maxW={'1200px'}>
      <HStack
        w={'full'}
        gap={{ base: 8, lg: 'auto' }}
        justifyContent={'space-between'}
        py={{ base: '40px', lg: '120px' }}
        flexWrap={{ base: 'wrap', lg: 'nowrap' }}
      >
        <Text maxW={'470px'} lineHeight={'28px'}>
          TripOAsia operates under industry-recognized licensing and
          certifications, ensuring our clients receive professional and
          dependable services. We prioritize transparency and customer
          satisfaction in every aspect of our business.
        </Text>
        <HStack
          w={'full'}
          alignItems={'center'}
          gap={{ base: '30px', lg: '56px' }}
        >
          <Image
            w={{ base: '55px', lg: '100px' }}
            src={'/icons/clin.svg'}
            alt='clin'
          />
          <Image
            w={{ base: '98px', lg: '166px' }}
            src={'/icons/true.svg'}
            alt='true'
          />
          <Image
            w={{ base: '44px', lg: '100px' }}
            src={'/icons/north_america.svg'}
            alt='north_america'
          />
          <Image
            w={{ base: '54px', lg: '100px' }}
            src={'/icons/iata.svg'}
            alt='iata'
          />
        </HStack>
      </HStack>
    </Container>
  );
}

export default Partners;
