import { Flex, Image, SimpleGrid, Stack, Text } from '@chakra-ui/react';
import React from 'react';

const data = [
  {
    icon: '/icons/support.svg',
    title: 'Personalised Service',
    description:
      'With over 20 live agents ready to assist, we offer tailored travel solutions to fit your unique preferences and needs.',
  },
  {
    icon: '/icons/deals.svg',
    title: 'Comprehensive Options',
    description:
      'From airline tickets and hotel packages to cruises, we provide end-to-end travel arrangements for a seamless experience',
  },
  {
    icon: '/icons/languages.svg',
    title: 'Language Support',
    description:
      'We speak your language! Our multilingual team communicates in English, Uzbek, Tajik, Russian, and Kazakh, breaking language barriers for a stress-free experience.',
  },
  {
    icon: '/icons/east-comfort.svg',
    title: 'Ease & Comfort',
    description:
      'Call us, and weâ€™ll take care of the rest. From planning to booking, we simplify your travel journey.',
  },
];

function InfoCard() {
  return (
    <SimpleGrid columns={{ base: 1, md: 2, lg: 4 }} gap={6}>
      {data?.map((el) => (
        <Stack key={el.icon}>
          <Flex
            flexDir={{ base: 'row', lg: 'column' }}
            gap={{ base: 3, md: 8 }}
          >
            <Image w={{ base: 7, md: '64px' }} src={el?.icon} alt='icon' />
            <Text fontWeight={700} fontSize={{ base: 16, md: 20 }}>
              {el?.title}
            </Text>
          </Flex>
          <Text
            color={'dark.100'}
            fontWeight={400}
            fontSize={{ base: 13, md: 16 }}
            lineHeight={{ base: '20px', lg: '26px' }}
          >
            {el?.description}
          </Text>
        </Stack>
      ))}
    </SimpleGrid>
  );
}

export default InfoCard;
