import { Box, Center, Heading, HStack, Image, Stack } from '@chakra-ui/react';
import React from 'react';
import Partners from './components/partners';
import Form from './components/form';

function Hero() {
  return (
    <Box
      // h={'100vh'}
      bgPos={'center'}
      bgRepeat={'no-repeat'}
      bgSize={'cover'}
      h={'873px'}
      backgroundImage={"url('/main-hero.webp')"}
    >
      <Center pt={'80px'} h={'full'} flexDir={'column'}>
        <HStack>
          <Stack
            justify={'center'}
            alignItems={{ base: 'center', md: 'start' }}
            gap={10}
          >
            <Heading
              color={'light'}
              fontSize={{ base: 26, md: 52 }}
              fontWeight={700}
            >
              Call Us, Pack Up, Take Off
            </Heading>
            <Form />
          </Stack>
          <Image
            src={'/plain_window.png'}
            objectFit={'contain'}
            display={{ base: 'none', md: 'block' }}
            alt='window'
            w={'555px'}
            h={'full'}
          />
        </HStack>
        <Partners />
      </Center>
    </Box>
  );
}

export default Hero;
