import { Box, Container, Text } from '@chakra-ui/react';
import React from 'react';
import ServiceCards from './components/card-group';

function Services() {
  return (
    <Container maxW={'1200px'} pt={{ base: '40px', lg: '64px' }} pb={'40px'}>
      <Text
        fontWeight={700}
        textAlign={'center'}
        fontSize={{ base: 18, lg: 44 }}
      >
        Services
      </Text>
      <Box mt={{ base: 4, lg: 8 }}>
        <ServiceCards />
      </Box>
    </Container>
  );
}

export default Services;
