import { Box, Container, HStack, Stack, Text } from '@chakra-ui/react';
import React from 'react';
import Form from './form';

function Exclusive() {
  return (
    <Container px={{ base: 0, lg: 8 }} maxW={'1300px'}>
      <Box
        // h={'422px'}
        w={'full'}
        bgSize={'cover'}
        bgPos={'center'}
        bgRepeat={'no-repeat'}
        rounded={{ base: 0, lg: 24 }}
        backgroundImage={'url(/sunrise_fly.png)'}
      >
        <HStack
          alignItems={'center'}
          flexWrap={'wrap'}
          w={'full'}
          px={{ base: 4, lg: 12 }}
          py={{ base: '40px', lg: 7 }}
          justifyContent={{ base: 'center', md: 'space-between' }}
          gap={{ base: 8, lg: 0 }}
        >
          <Stack
            h={'full'}
            justifyContent={'space-between'}
            maxW={'383px'}
            color={'light'}
          >
            <Text fontWeight={700} fontSize={{ base: 18, lg: 40 }}>
              Unlock Exclusive Flight Deals!
            </Text>
            <Text fontSize={{ base: 14, lg: 18 }}>
              Join over 200,000 savvy travelers who get first access to insider
              discounts, flash sales, and secret airfare deals!
            </Text>
            <Box
              mt={{ base: 8, lg: '50px' }}
              as='ul'
              ml={0}
              pl='20px'
              fontSize={15}
              fontWeight={400}
              listStyleType='disc'
            >
              <li>Save up to 50% on flights</li>
              <li>Get notified before prices go up</li>
              <li>Access VIP-only travel perks</li>
            </Box>
          </Stack>
          <Box>
            <Form />
          </Box>
        </HStack>
      </Box>
    </Container>
  );
}

export default Exclusive;
