import { Box, Image, Text } from '@chakra-ui/react';
import React from 'react';

export default function ServiceCard({
  title,
  imageSrc,
}: // gridArea,
// width,
{
  title: string;
  imageSrc: string;
  // gridArea: string;
  // width: string;
}) {
  return (
    <Box
      // gridArea={gridArea}
      // gridColumn={width}
      position={'relative'}
      cursor={'pointer'}
      transition={'all 0.3s ease'}
      _hover={{
        '&::before': {
          opacity: 1,
        },
        '& .arrow-box': {
          bg: 'red.500',
          color: 'white',
          px: 8,
        },
      }}
      _before={{
        content: '""',
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        bg: '#1C1B1B4D',
        borderRadius: '24px',
        opacity: 0,
        transition: 'opacity 0.3s ease',
        zIndex: 1,
      }}
    >
      <Image
        src={imageSrc}
        rounded={24}
        alt={title}
        w='100%'
        h='100%'
        minH={'160px'}
        objectFit='cover'
      />

      <Box
        className='arrow-box'
        pl={3}
        pr={4}
        py={2}
        left={0}
        bg='white'
        display='flex'
        w='fit-content'
        alignItems='center'
        position={'absolute'}
        top={{ base: 4, lg: 6 }}
        clipPath='polygon(0 0, calc(100% - 15px) 0, 100% 50%, calc(100% - 15px) 100%, 0 100%)'
        transition={'all 0.3s ease'}
        transformOrigin={'left center'}
        zIndex={2}
      >
        <Text
          fontSize={{ base: 14, md: '18px' }}
          fontWeight={600}
          color='current'
          letterSpacing='tight'
          transition={'color 0.3s ease'}
        >
          {title}
        </Text>
      </Box>
    </Box>
  );
}
